var hostname; var protocol; var port; var ssoUrl;
if (process.env.REACT_APP_ENV == "LOCAL") {
    // hostname="150.136.150.211";
    // port=":8001";
    // protocol="http:";
    hostname = "150.136.254.222";
    port = ":8443/api";
    protocol = "http:";    
    ssoUrl = global.baseDom + '/ssoheader.php';
    // ssoUrl = 'https://run.mocky.io/v3/156abd5c-7046-478c-8e7d-e11354682b9d';
} else {
    hostname = window.location.hostname+'/api';
    protocol = window.location.protocol;
    port = "";
    ssoUrl = global.baseDom + '/ssoheader.php';
    //ssoUrl = 'https://run.mocky.io/v3/156abd5c-7046-478c-8e7d-e11354682b9d';
}
export default {
    REACT_APP_IP: hostname,
    REACT_APP_P: protocol + '//',
    REACT_APP_Port: port,
    REACT_SSO_URL: ssoUrl
};