
import React, { useState, Component } from 'react';
import { DropdownButton, Dropdown, Button } from 'react-bootstrap';
import Select from 'react-select';
import { colourOptions, groupedOptions } from './data';

import './index.scss';

export class Search extends Component {
  constructor(props) {
    super(props);
    this.handleChange = this.handleChange.bind(this);
    this.handleSelect = this.handleSelect.bind(this);
    this.search = this.search.bind(this);
    this.state = {
      searchText: '',
      AssetType: 'Asset Type'
    }
  }
  handleChange = ({ target }) => {
    this.setState({ [target.name]: target.value });
  };
  handleSelect(evt, evtKey) {
    this.setState({ AssetType: evt });
  }
  search(e) {
    e.preventDefault();
    console.log(this.state.searchText)
    var AssetTypeId;
    switch (this.state.AssetType) {
      case "Documentation":
        AssetTypeId = 'ulk7ocph9i'
        break;
      case "Technical Resources":
        AssetTypeId = 'dvnwrcm859'
        break;
      case "Win-Related Collateral":
        AssetTypeId = 'raig04nlko'
        break;
      case "Deployable Resources":
        AssetTypeId = '7ih1fhcl93'
        break;
      case "Sale Resources":
        AssetTypeId = 'a3eay8q22f'
        break;
    }
    this.props.searchfn(AssetTypeId, this.state.searchText)
  }
  render() {
    return (
      <div style={{ marginTop: '1rem' }}>
        <div class="searchContainer">
          {/* <form> */}
            <div class="col-md-11 offset-md-1 mt-5">
              <div class="input-group mb-3">
                <div class="input-group-prepend">
                  <div href="javascript:void(0)" class="input-group-text asset-type">
                    <DropdownButton id="dropdown-item-button" title={this.state.AssetType} onSelect={this.handleSelect}>
                      <Dropdown.Item as="button" eventKey="All">All</Dropdown.Item>
                      <Dropdown.Item as="button" eventKey="Documentation">Documentation</Dropdown.Item>
                      <Dropdown.Item as="button" eventKey="Technical Resources">Technical Resources</Dropdown.Item>
                      <Dropdown.Item as="button" eventKey="Win-Related Collateral">Win-Related Collateral</Dropdown.Item>
                      <Dropdown.Item as="button" eventKey="Deployable Resources">Deployable Resources</Dropdown.Item>
                      <Dropdown.Item as="button" eventKey="Sale Resources">Sale Resources</Dropdown.Item>
                    </DropdownButton>
                  </div>
                </div>
                <input type="text" class="form-control form-control-lg" name="searchText" id="searchText" value={this.state.searchText} onChange={this.handleChange} placeholder="Search by Industry, Customer Name or Workload" aria-label="Recipient's username" />
                <div class="input-group-append">
                  <button class="input-group-text" href="javascript:void(0)" onClick={this.search} type="submit">
                    <img width="20px" src={process.env.PUBLIC_URL + '/images/Search-icon.png'} alt="North America Sales" />
                  </button>
                </div>
              </div>
            </div>
          {/* </form> */}
        </div>
      </div>
    )
  }
}
export default Search;
