
import React, { useState, Component } from 'react';
import { Card, Col, Nav } from 'react-bootstrap';

import './index.scss';

export class AssetCard extends Component {
  constructor(props) {
    super(props);
    //console.log(this.props.CardData)
    //"https://via.placeholder.com/300.png/09f/fff%20C/O"

  }
  render() {
    return (

      <Card style={{ marginTop: '1rem' }}>
        <Nav.Link href={'/details/?' + this.props.CardData.ASSET_ID}>
          <Card.Img variant="top" src={this.props.CardData.ASSET_THUMBNAIL} />
          <Card.Body>
            <Card.Title>{this.props.CardData.ASSET_CUSTOMER}</Card.Title>
            <Card.Text>
              {this.props.CardData.ASSET_DESCRIPTION}
            </Card.Text>
            {/* <span class="float-right mt-4">{this.props.CardData.ASSET_COUNT} Assets</span> */}
          </Card.Body>
        </Nav.Link>
      </Card >

    );
  }
}

export default AssetCard;
