
import React from 'react'; 
import './index.scss';

function PrevPage() {
    function FuncRedirect(event) {
        window.history.go(-1);
    }
  return (
    <div class="mb-4">
        <a onClick={FuncRedirect} href="javascript:void(0);">Back to Previous</a>
    </div>
  );
}

export default PrevPage;
