const appconfig = require('./appconfig').default;
// import appconfig from './appconfig';
const axios = require('axios');


function getRequest(endpoint, header, mocky) {
    if (mocky != true) {
        var baseurl = appconfig.REACT_APP_P + appconfig.REACT_APP_IP + appconfig.REACT_APP_Port;

        var xhrUrl = baseurl + endpoint;
    } else {
        var xhrUrl = endpoint;
    }
    return new Promise(function (resolve, reject) {
        axios.get(xhrUrl, {
            headers: header
        }).then(function (response) {

            setTimeout(() => resolve(response), 1000);
            // setTimeout(() => reject(response), 1000);
        })
            .catch(function (error) {
                setTimeout(() => reject(error), 1000);
            });

        // the function is executed automatically when the promise is constructed

        // after 1 second signal that the job is done with the result "done"
    });

}

