import React, {
    useState,
    Component
} from 'react';

import './index.scss';
import {
    Tabs,
    Tab,
    Table,
    Button,
    Image,Spinner
} from 'react-bootstrap';
import {
    BrowserRouter as Router,
    Route,
    Link,
    Redirect,
    withRouter
} from "react-router-dom";
const request = require('../../networkHandler.js');

export class MyAsset extends Component {
    state = {
        loading: true,
        AssetDataLive: [],
        AssetUnderReview: [],
        PendingRectification: [],
        AssetRejected: [],
        AssetSaved: [],
        redirectPage: '',
        AssetID: '',
        redirectEditPage: '',
        classDeploy: '',
        redirectDeployPage: '',
        handleViewNote: ''
    }
    componentDidMount() {
        localStorage.setItem('selectedlist', "");

        localStorage.setItem('selectedQues', "");

        localStorage.setItem('LVL', "");
        if ((localStorage.getItem('user_name') != "") && (localStorage.getItem('user_name') != null) && (localStorage.getItem('user_name') != undefined)) {


            let headers = {
                // "user_email": localStorage.getItem("user_email")

            }
            let endpoint = '/asset/myAssets';
            request.getMyAssetData(endpoint, headers, false).then(MyAssetData => {
                for (var i = 0; i < MyAssetData.data.length; i++) {

                    if (MyAssetData.data[i].status == "Live") {
                        for (var j = 0; j < MyAssetData.data[i].arr.length; j++) {
                            if (MyAssetData.data[i].arr[j].ASSET_REVIEW_NOTE == null) {
                                MyAssetData.data[i].arr[j].ASSET_REVIEW_NOTE = [];
                            }
                        }
                        const AssetDataLive = MyAssetData.data[i].arr;
                        this.setState({
                            loading: false,
                            AssetDataLive
                        });

                    }
                    if (MyAssetData.data[i].status == "Pending Review") {
                        for (var j = 0; j < MyAssetData.data[i].arr.length; j++) {
                            if (MyAssetData.data[i].arr[j].ASSET_REVIEW_NOTE == null) {
                                MyAssetData.data[i].arr[j].ASSET_REVIEW_NOTE = [];
                            }
                        }
                        const AssetUnderReview = MyAssetData.data[i].arr;
                        this.setState({
                            loading: false,
                            AssetUnderReview
                        });

                    }
                    if (MyAssetData.data[i].status == "Pending Rectification") {
                        for (var j = 0; j < MyAssetData.data[i].arr.length; j++) {
                            if (MyAssetData.data[i].arr[j].ASSET_REVIEW_NOTE == null) {
                                MyAssetData.data[i].arr[j].ASSET_REVIEW_NOTE = [];
                            }
                        }
                        const PendingRectification = MyAssetData.data[i].arr;
                        this.setState({
                            loading: false,
                            PendingRectification
                        });

                    }
                    if (MyAssetData.data[i].status == "Reject") {
                        for (var j = 0; j < MyAssetData.data[i].arr.length; j++) {
                            if (MyAssetData.data[i].arr[j].ASSET_REVIEW_NOTE == null) {
                                MyAssetData.data[i].arr[j].ASSET_REVIEW_NOTE = [];
                            }
                        }
                        const AssetRejected = MyAssetData.data[i].arr;
                        this.setState({
                            loading: false,
                            AssetRejected
                        });

                    }
                    if (MyAssetData.data[i].status == "Saved") {
                        for (var j = 0; j < MyAssetData.data[i].arr.length; j++) {
                            if (MyAssetData.data[i].arr[j].ASSET_REVIEW_NOTE == null) {
                                MyAssetData.data[i].arr[j].ASSET_REVIEW_NOTE = [];
                            }
                        }
                        const AssetSaved = MyAssetData.data[i].arr;
                        this.setState({
                            loading: false,
                            AssetSaved
                        });

                    }
                }


            }).catch(error => {
                this.setState({ loading: false })
                console.log("i am in catch" + error);
                //localStorage.clear();
            });





        }
    }

    LiveView = id => event => {
        localStorage.setItem('checklist', "");
        localStorage.setItem('selectedlist', "");
        localStorage.setItem('LVL', "");
        this.setState({
            redirectPage: true,
            AssetID: id
        });
    }
    detailView = id => event => {

        this.setState({
            handleViewNote: true,
            AssetID: id
        });
    }
    handleEdit = (type, id) => event => {
        if (type == 'PendingRectification') {

            var result = this.state.PendingRectification.filter(element => ((element.ASSET_ID == id)));
            var jsonP = result[0].ASSET_REVIEW_NOTE;
            if (jsonP.note != "" && jsonP.note != null && jsonP.note != "undefined") {

                localStorage.setItem('selectedlist', jsonP.note);

            } else {
                localStorage.setItem('selectedlist', "");

            }
        }
        this.setState({
            redirectEditPage: true,
            EditAssetID: id
        });
    }
    handledeploy = id => event => {

        this.setState({
            redirectDeployPage: true,
            deployAssetID: id
        });

    }
    handleView = (type, id) => event => {
        if (type == "AssetUnderReview") {
            var result = this.state.AssetUnderReview.filter(element => ((element.ASSET_ID == id)));
            //  console.log(result);

            localStorage.setItem('checklist', "");
            localStorage.setItem('selectedlist', result[0].ASSET_REVIEW_NOTE.note);
            localStorage.setItem('LVL', result[0].ASSET_APPROVAL_LVL);
            this.setState({
                handleUnderView: true,
                AssetID: id
            });
            //console.log(result[0].CHECKLIST_ITEMS);
            // global.checklist=result[0].CHECKLIST_ITEMS;
        }
        if (type == "PendingRectification") {
            var result = this.state.PendingRectification.filter(element => ((element.ASSET_ID == id)));
            console.log(result);

            localStorage.setItem('checklist', ""); // No checklist will be shown for MYASSET
            localStorage.setItem('status', "Pending Rectification");

            localStorage.setItem('selectedlist', result[0].ASSET_REVIEW_NOTE.note);
            localStorage.setItem('LVL', result[0].ASSET_APPROVAL_LVL);

            // global.checklist=result[0].CHECKLIST_ITEMS;
        }

        if (type == "AssetSaved") {
            var result = this.state.AssetSaved.filter(element => ((element.ASSET_ID == id)));

            localStorage.setItem('checklist', "");
            localStorage.setItem('selectedlist', result[0].ASSET_REVIEW_NOTE.note);
            localStorage.setItem('LVL', result[0].ASSET_APPROVAL_LVL)

            // global.checklist=result[0].CHECKLIST_ITEMS;
        }
        if (type == "AssetRejected") {
            var result = this.state.AssetRejected.filter(element => ((element.ASSET_ID == id)));
            localStorage.setItem('checklist', "");
            localStorage.setItem('selectedlist', result[0].ASSET_REVIEW_NOTE.note);
            localStorage.setItem('LVL', result[0].ASSET_APPROVAL_LVL)
            localStorage.setItem('status', "Reject");

            // global.checklist=result[0].CHECKLIST_ITEMS;
        }
        this.setState({
            handleViewNote: true,
            AssetID: id
        });

        global.Governance = 'Viewnote'
    }

    render() {

        if (this.state.redirectPage) {
            return <Redirect push to={
                "/details/?" + this.state.AssetID
            }
            />;
        }
        if (this.state.handleUnderView) {
            return <Redirect push to={
                "/details/?" + this.state.AssetID + "&MyASSET=N"
            }
            />;
        }
        if (this.state.redirectDeployPage) {
            return <Redirect push to={
                "/deploy/?" + this.state.deployAssetID
            }
            />;
        }
        if (this.state.redirectEditPage) {
            // window.location.href="/AssetManagement/?" + this.state.EditAssetID
            return <Redirect push to={
                "/create?" + this.state.EditAssetID + "&MyASSET=Y"
            }
            />;
        }
        if (this.state.handleViewNote) {
            return <Redirect push to={
                "/details/?" + this.state.AssetID + "&MyASSET=Y"
            }
            />;
        }
        var AssetDataLivenew;
        var AssetUnderReviewnew;
        var PendingRectificationnew;
        var AssetRejectednew;
        var AssetSavednew;

        //   if(global.assetData!=undefined){
        //     AssetDatanew=global.assetData;  
        //   }else{
        AssetDataLivenew = this.state.AssetDataLive;
        AssetUnderReviewnew = this.state.AssetUnderReview;
        PendingRectificationnew = this.state.PendingRectification;
        AssetRejectednew = this.state.AssetRejected;
        AssetSavednew = this.state.AssetSaved;

        var LiveCnt = "";
        if (AssetDataLivenew.length > 0) {
            LiveCnt = "Live Assets (" + AssetDataLivenew.length + ")";
        } else {
            LiveCnt = "Live Assets";
        }
        var UnderReviewCnt;
        if (AssetUnderReviewnew.length > 0) {
            UnderReviewCnt = "Under Review (" + AssetUnderReviewnew.length + ")";
        } else {
            UnderReviewCnt = "Under Review";
        }
        var PendingRectificationCnt;
        if (PendingRectificationnew.length > 0) {
            PendingRectificationCnt = "Requires Rectification (" + PendingRectificationnew.length + ")";
        } else {
            PendingRectificationCnt = "Requires Rectification";
        }
        var AssetRejectednewCnt;
        if (AssetRejectednew.length > 0) {
            AssetRejectednewCnt = "Rejected (" + AssetRejectednew.length + ")";
        } else {
            AssetRejectednewCnt = "Rejected";
        }

        var AssetSavednewCnt;
        if (AssetSavednew.length > 0) {
            AssetSavednewCnt = "Saved (" + AssetSavednew.length + ")";
        } else {
            AssetSavednewCnt = "Saved";
        }

        return (
            <div className="mb-5">
                {this.state.loading && <div className="overlay"><Spinner animation="border" variant="danger" /></div>}
                <Tabs defaultActiveKey="pendingReview" id="uncontrolled-tab-example">
                    <Tab eventKey="pendingReview" title={UnderReviewCnt}>
                        <div class="pt-5">
                            <Table borderless>
                                {AssetUnderReviewnew.length > 0 && <>
                                    <thead>
                                        <tr>
                                            <th>Customer</th>
                                            <th>Asset ID</th>
                                            <th>Asset Type</th>
                                            <th>Asset Owner</th>
                                            <th>Created On</th>
                                            <th>Action</th>
                                        </tr>
                                    </thead>
                                    {AssetUnderReviewnew.map((AssetUnderReview, index) =>
                                        <tbody>
                                            <tr>
                                                <td width="142"><Image src={AssetUnderReview.ASSET_THUMBNAIL} thumbnail /></td>
                                                <td>{AssetUnderReview.ASSET_ID}</td>
                                                <td>{AssetUnderReview.ASSET_TYPE}</td>
                                                <td>{AssetUnderReview.ASSET_OWNER.split('\n').map((item, key) => {
                                                    return <span key={key}>{item}<br /></span>
                                                })}
                                                </td>
                                                <td>{new Date(AssetUnderReview.ASSET_CREATED_DATE).toLocaleDateString()}</td>
                                                <td>
                                                    <p className="mb-2">
                                                        <Button variant="danger" size="sm" block onClick={this.handleView('AssetUnderReview', AssetUnderReview.ASSET_ID)}>View</Button>
                                                    </p>
                                                    <Button variant="danger" size="sm" block onClick={this.handleEdit("AssetUnderReview", AssetUnderReview.ASSET_ID)}>Edit</Button>
                                                </td>
                                            </tr>
                                        </tbody>
                                    )}
                                </>}{AssetUnderReviewnew.length <= 0 &&
                                    <p class="text-center p15">
                                        No Records Found.
                    </p>
                                }
                            </Table>
                        </div>
                    </Tab>
                    <Tab eventKey="rectificationRequested" title={PendingRectificationCnt}>
                        <div class="pt-5">
                            <Table borderless>
                                {PendingRectificationnew.length > 0 && <>
                                    <thead>
                                        <tr>
                                            <th>Customer</th>
                                            <th>Asset ID</th>
                                            <th>Asset Type</th>
                                            <th>Asset Owner</th>
                                            <th>Created On</th>
                                            <th>Action</th>
                                        </tr>
                                    </thead>
                                    {PendingRectificationnew.map((PendingRectification, index) =>
                                        <tbody>
                                            <tr>
                                                <td width="142">
                                                    <Image src={PendingRectification.ASSET_THUMBNAIL} thumbnail />
                                                </td>

                                                <td>{PendingRectification.ASSET_ID}</td>
                                                <td>{PendingRectification.ASSET_TYPE}</td>
                                                <td>{PendingRectification.ASSET_OWNER.split('\n').map((item, key) => {
                                                    return <span key={key}>{item}<br /></span>
                                                })}
                                                </td>
                                                <td>{new Date(PendingRectification.ASSET_CREATED_DATE).toLocaleDateString()}</td>
                                                <td>
                                                    <p className="mb-2">
                                                        <Button variant="danger" size="sm" block onClick={this.handleView('PendingRectification', PendingRectification.ASSET_ID)}>View</Button>
                                                    </p>
                                                    <Button variant="danger" size="sm" block onClick={this.handleEdit("PendingRectification", PendingRectification.ASSET_ID)}>Edit</Button>
                                                </td>
                                            </tr>
                                        </tbody>
                                    )}
                                </>}{PendingRectificationnew.length <= 0 &&
                                    <p class="text-center p15">
                                        No Records Found.
                    </p>
                                }
                            </Table>
                        </div>
                    </Tab>
                    <Tab eventKey="rejected" title={AssetRejectednewCnt}>
                        <div class="pt-5">
                            <Table borderless>
                                {AssetRejectednew.length > 0 && <>
                                    <thead>
                                        <tr>
                                            <th>Customer</th>
                                            <th>Asset ID</th>
                                            <th>Asset Type</th>
                                            <th>Asset Owner</th>
                                            <th>Created On</th>
                                            <th>Action</th>
                                        </tr>
                                    </thead>
                                    {AssetRejectednew.map((AssetRejected, index) =>
                                        <tbody>
                                            <tr>
                                                <td width="142">
                                                    <Image src={AssetRejected.ASSET_THUMBNAIL} thumbnail />
                                                </td>
                                                <td>{AssetRejected.ASSET_TITLE}</td>
                                                <td>{AssetRejected.ASSET_ID}</td>
                                                <td>{AssetRejected.ASSET_TYPE}</td>
                                                <td>{AssetRejected.ASSET_OWNER.split('\n').map((item, key) => {
                                                    return <span key={key}>{item}<br /></span>
                                                })}
                                                </td>
                                                <td>{new Date(AssetRejected.ASSET_CREATED_DATE).toLocaleDateString()}</td>
                                                <td>
                                                    <p className="mb-2">
                                                        <Button variant="danger" size="sm" block onClick={this.handleView('AssetRejected', AssetRejected.ASSET_ID)}>View</Button>
                                                    </p>
                                                    <Button variant="danger" size="sm" block onClick={this.handleEdit("AssetRejected", AssetRejected.ASSET_ID)}>Edit</Button>
                                                </td>
                                            </tr>
                                        </tbody>
                                    )}
                                </>}{AssetRejectednew.length <= 0 &&
                                    <p class="text-center p15">
                                        No Records Found.
                    </p>
                                }
                            </Table>
                        </div>
                    </Tab>
                    <Tab eventKey={LiveCnt} title={LiveCnt}>
                        <div class="pt-5">
                            <Table borderless>
                                {AssetDataLivenew.length > 0 && <>
                                    <thead>
                                        <tr>
                                            <th>Customer</th>
                                            <th>Asset ID</th>
                                            <th>Asset Type</th>
                                            <th>Asset Owner</th>
                                            <th>Created On</th>
                                            <th>Action</th>
                                        </tr>
                                    </thead>
                                    {AssetDataLivenew.map((AssetDataLive, index) =>
                                        <tbody>
                                            <tr>
                                                <td width="142">
                                                    <Image src={AssetDataLive.ASSET_THUMBNAIL} thumbnail />
                                                </td>
                                                <td>{AssetDataLive.ASSET_TITLE}</td>
                                                <td>{AssetDataLive.ASSET_ID}</td>
                                                <td>{AssetDataLive.ASSET_TYPE}</td>
                                                <td>
                                                    {AssetDataLive.ASSET_OWNER}
                                                    {/* {AssetSavednew.ASSET_OWNER.split('\n').map((item, key) => {
                                                    return <span key={key}>{item}<br /></span>
                                                })} */}
                                                </td>
                                                <td>{new Date(AssetDataLive.ASSET_CREATED_DATE).toLocaleDateString()}</td>
                                                <td>
                                                    <p className="mb-2">
                                                        <Button variant="danger" size="sm" block onClick={this.handleView('AssetDataLive', AssetDataLive.ASSET_ID)}>View</Button>
                                                    </p>
                                                    <Button variant="danger" size="sm" block onClick={this.handleEdit("AssetDataLive", AssetDataLive.ASSET_ID)}>Edit</Button>
                                                </td>
                                            </tr>
                                        </tbody>
                                    )}
                                </>}{AssetDataLivenew.length <= 0 &&
                                    <p class="text-center p15">
                                        No Records Found.
                    </p>
                                }
                            </Table>
                        </div>
                    </Tab>
                    <Tab eventKey={AssetSavednewCnt} title={AssetSavednewCnt}>
                        <div class="pt-5">
                            <Table borderless>
                                {AssetSavednew.length > 0 && <>
                                    <thead>
                                        <tr>
                                            <th>Customer</th>
                                            <th>Asset ID</th>
                                            <th>Asset Type</th>
                                            <th>Asset Owner</th>
                                            <th>Created On</th>
                                            <th>Action</th>
                                        </tr>
                                    </thead>
                                    {AssetSavednew.map((AssetSaved, index) =>
                                        <tbody>
                                            <tr>
                                                <td width="142">
                                                    <Image src={AssetSaved.ASSET_THUMBNAIL} thumbnail />
                                                </td>
                                                <td>{AssetSaved.ASSET_TITLE}</td>
                                                <td>{AssetSaved.ASSET_ID}</td>
                                                <td>{AssetSaved.ASSET_TYPE}</td>
                                                <td>
                                                    {AssetSaved.ASSET_OWNER}
                                                    {/* {AssetSavednew.ASSET_OWNER.split('\n').map((item, key) => {
                                                    return <span key={key}>{item}<br /></span>
                                                })} */}
                                                </td>
                                                <td>{new Date(AssetSaved.ASSET_CREATED_DATE).toLocaleDateString()}</td>
                                                <td>
                                                    <p className="mb-2">
                                                        <Button variant="danger" size="sm" block onClick={this.handleView('AssetSaved', AssetSaved.ASSET_ID)}>View</Button>
                                                    </p>
                                                    <Button variant="danger" size="sm" block onClick={this.handleEdit("AssetSaved", AssetSaved.ASSET_ID)}>Edit</Button>
                                                </td>
                                            </tr>
                                        </tbody>
                                    )}
                                </>}{AssetSavednew.length <= 0 &&
                                    <p class="text-center p15">
                                        No Records Found.
                    </p>
                                }
                            </Table>
                        </div>
                    </Tab>
                </Tabs>
            </div>)
    }
}

export default MyAsset