
import React, { useState, Component } from 'react';
import { Card, Col } from 'react-bootstrap';

import './index.scss';

export class CustomCard extends Component {
  constructor(props) {
    super(props);
  }
  render() {
    return (
      <Card style={{ marginTop: '1rem' }} onClick={()=>this.props.callfun(this.props.CardData.FILTER_ID,'',this.props.CardData.FILTER_NAME)}>
        <Card.Img variant="top" src={this.props.CardData.FILTER_IMAGE}/>
        <Card.Body>
          <Card.Title>{this.props.CardData.FILTER_NAME}</Card.Title>
          <Card.Text>
            <div style={{ maxWidth: '75%', float: 'left' }}>View all {this.props.CardData.FILTER_NAME} assets</div>
        </Card.Text>
          <p class="float-right mt-4 mb-0">{this.props.CardData.ASSET_COUNT} Assets</p>
        </Card.Body>
      </Card >
    );
  }
}
export default CustomCard;
